import React from 'react';
import './tutorial/tutorial.scss';
import firebase from '../services/firebase.services';
import { Post } from '../components/post.component';
import { Filter } from '../helpers/filter'
import { cache_posts, get_cache_posts } from '../services/localstorage.services'
export class TutorialPage extends React.Component {
    constructor() {
        super();
        this.state = {
            posts: [],
            categories: [],
            filter: {
                categories: []
            },
            loading: true
        }

    }
    componentDidMount() {
        this.loadCategories();
        this.loadPost();
    }
    loadCategories() {
        firebase
            .model('categories')
            .fetchLikeObject()
            .then(record => {
                const categories = Object.keys(record);
                this.setState({
                    categories
                })
            })
    }
    loadPostWithFilter = () => {
        const categories = this.state.filter.categories;
        const posts = get_cache_posts();
        const postsWithFilter = posts.filter(post => {
            const cats = post.cats;
            const listSame = cats.filter(cat => -1 !== categories.indexOf(cat));
            return listSame.length > 0;
        });
        this.setState({
            posts: postsWithFilter
        })
    }
    loadPost() {
        if (this.state.filter.categories.length > 0) {
            return this.loadPostWithFilter()
        } else {
            this.setState({
                loading: true
            })
            return firebase
                .model('posts')
                .fetch()
                .then(records => {
                    const posts = new Filter(records).orderBy(
                        'post_date DESC'
                    );
                    cache_posts(posts);
                    this.setState({
                        posts,
                        loading: false
                    })
                })
        }
    }
    isCheck = (category) => {
        const filter = this.state.filter
        const { categories } = filter;
        return categories.indexOf(category) !== -1;
    }
    onFilter = (e) => {
        const category = e.target.id;
        const isEnable = e.target.checked;
        const filter = this.state.filter;
        const categories = [...filter.categories];
        const isExist = categories.indexOf(category)
        if (isEnable) {
            if (isExist === -1) {
                categories.push(category)
            }
        } else {
            if (isExist !== -1) {
                categories.splice(isExist, 1);
            }
        }
        return this.setState({
            filter: {
                categories: [...categories]
            }
        }, this.loadPost)
    }
    render() {
        return (
            <div className='row'>
                <section className='col-lg-3'>
                    <div className='filter col-lg-12 toolbox'>
                        <div className='toolbox_title d-flex justify-content-between '>
                            Filter
                            <img className='icon clickable d-none' alt='back-btn' src='assets/back-icon.png' />
                        </div>
                        <ul className='list-group toolbox_list'>
                            {this.state.categories.map(
                                category =>
                                    (
                                        <li className='d-flex justify-content-between' key={category}>
                                            <input type='checkbox' id={category} checked={this.isCheck(category)} onChange={this.onFilter} />
                                            <label htmlFor={category}>{category}</label>
                                        </li>
                                    ))}

                        </ul>
                    </div>


                    {/*<div className='filter col-lg-12 toolbox mt-5'>
                        <div className='toolbox_title d-flex justify-content-between'>
                            Filters
                            <img className='icon clickable' alt='back-btn' src='assets/back-icon.png' />
                        </div>
                        <ul className='list-group toolbox_list'>
                            <li className='d-flex justify-content-between'>
                                <input type='checkbox' id='input' />
                                <label for='input'>Javascript</label>
                            </li>
                            <li className='d-flex justify-content-between'>
                                <input type='checkbox' />
                                <label>Javascript</label>
                            </li>
                            <li className='d-flex justify-content-between'>
                                <input type='checkbox' />
                                <label>Javascript</label>
                            </li>
                            <li className='d-flex justify-content-between'>
                                <input type='checkbox' />
                                <label>Javascript</label>
                            </li>
                        </ul>
                                    </div> */}
                </section>


                <section className='offset-lg-1 col-lg-8'>

                    {this.state.loading ? <div className='row mt-2' >
                        <div className="ld ld-ring ld-cycle m-auto"></div>
                    </div> : this.state.posts.map(
                        post =>
                            <Post
                                key={post.id}
                                post={post} />)}
                </section>
            </div>
        )
    }
}