
const filters = require('loopback-filters');
export class Filter {
    constructor(data) {
        this.data = data;
    }
    /**
     * 
     * @param {Array} orderBy 
     */
    orderBy(orderBy) {
        return filters(this.data, {
            order: orderBy
        })
    }
}
export function getDateString(dateString) {
    if (!dateString) return null;
    const d = new Date(dateString.split("-").join("/"));
    const year = d.getFullYear();
    const month = d.getMonth() + 1;
    const date = d.getDate();
    return `${date}/${month}/${year}`;
}