/**
 * @param {string} item
 * @param {object} data
 */
function set(item, data) {
    window.localStorage.setItem(item, JSON.stringify(data));
}
/**
 * 
 * @param {string} item 
 */
function get(item) {
    return JSON.parse(window.localStorage.getItem(item))
}
/**
 * Cache Lists Posts to LocalStorage
 * @param {array} posts 
 */
export const localStorageName = {
    posts: 'posts'
}
export function cache_posts(posts) {
    set(localStorageName.posts, posts);
}
export function get_cache_posts() {
    return get(localStorageName.posts);
}
/**
 * get Cache port if exists
 * @param {number} post_id 
 * @return {object}
 */
export function get_cache_post(post_id) {
    const posts = get(localStorageName.posts);
    if (!posts) return undefined;
    return posts.find(c => c.ID === Number(post_id));
}

export function get_cache_post_by_post_name(postname) {
    const posts = get(localStorageName.posts);
    if (!posts) return undefined;
    return posts.find(c => c.post_name === postname);
}