import React from 'react';
import firebase from '../services/firebase.services';
import { get_cache_post_by_post_name, cache_posts } from '../services/localstorage.services';
import { getDateString } from '../helpers/filter'
import { Comments, Like } from 'react-facebook';
export class PostDetailPage extends React.Component {
    constructor() {
        super();
        this.state = {
            post: {}
        }
    }
    getRealPostByName = (post_name) => {
        return new Promise((resolve, reject) => {

            firebase.model('/posts').fetch()
                .then(posts => {
                    cache_posts(posts);
                    const post = posts.find(c => c.post_name = post_name);
                    if (!post) {
                        reject({
                            type: 'not found'
                        });
                        return;
                    }
                    else resolve(post);
                })
        })
    }
    getPost = (name) => {
        let post = get_cache_post_by_post_name(name);
        if (!post) {
            this
                .getRealPostByName(name)
                .then(post => this.setState({ post }))
        } else {
            this.setState({
                post
            })
        }
    }
    componentDidMount() {
        const id = this.props.match.params.id;
        this.getPost(id);
    }
    render() {
        return (
            <div>
                <h1>{this.state.post.post_title}</h1>
                <p style={{
                    marginBottom: '.5rem'
                }}><small>{getDateString(this.state.post.post_date)}</small></p>
                <p style={{
                    marginBottom: '.5rem'
                }}><small>{this.state.post.cats ? this.state.post.cats.join(' | ') : ''}</small></p>
                <Like showFaces share />
                <div className='the_content container' dangerouslySetInnerHTML={{
                    __html: this.state.post.post_content
                }}>

                </div>
                <Comments href={window.location.href} width="100%" />
            </div>
        )
    }
}