import React from 'react';
import { Link } from 'react-router-dom'
import { getDateString } from '../helpers/filter'
export class Post extends React.Component {
    render() {
        return (
            <div className='post shadow'>
                <div className='post_order d-flex justify-content-center align-items-center d-sm-none'>{this.props.post.ID}</div>
                <h4 className='post_title'>{this.props.post.post_title}</h4>
                <small>{getDateString(this.props.post.post_date)}</small>
                <p style={{
                    marginBottom: '.5rem',
                    fontWeight: 'bold',
                    textDecoration: 'underline'
                }}><small>{this.props.post.cats.join(' | ')}</small></p>
                <div className='post_description'>{this.props.post.post_excerpt}
                </div>
                <Link className='bg-black btn-read' to={`/posts/${this.props.post.post_name}`}>Read More...</Link>
            </div>
        )
    }
}